footer{
    background-color: $gray-300;
    padding: 5rem 0;
    width: 100%;
    max-width: 500rem;    
    .container{
        @include flex(column, center, center);
    }
    .info-area{
        width: 100%;
        @include flex(row, center, space-between);
        .logo{
                padding-bottom: 2rem;
                img{
                    width: 25rem;
                }

            }

            .social-area{
                padding-top: 2rem;
                border-left: 1px solid $color-button;
                padding-left: 42rem;
                display: flex;
                align-items: center;
                ul{
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    
                    li{
                        @include flex(row, center, flex-start);
                        &:not(:last-child){
                        margin-bottom: 3rem;
                    }
                    img{
                        width: 3rem;
                        margin-right: 2rem
                        
                    }
                    a{
                        font-family: 'Poppins', sans-serif;
                        font-size: 1.6rem;
                        font-weight: 500;
                        color: $color-button;
                        opacity: 0.7;
                        &:hover{
                            opacity: 0.5;
                        }
                    }
                }
            }
        }        
    }
    span{
        font-family: 'Poppins', sans-serif;
        color: $color-button;
        margin-top: 2rem;
        font-size: 1.6rem;
        font-weight: 500;
    }
    
}